	import Vue                    from 'vue';
	import querystring            from 'querystring';
	import {mapState, mapActions} from 'vuex';

	export default{
		name : 'collect',
		data() {
			return {
				loading : false,
				isOk    : false,
				total   : 1,
			}
		},
		props : {
			product : {
				type    : Object,
				default : () => {return {}}
			},
			labels : {
				type    : Array,
				default : () => {return []}
			}
		},
		methods: {
			...mapActions([
                'AddCart',
                'ShowLogin',
            ]),
			increment() {
            	this.total += 1
            },
            decrement() {
            	if (this.total < 1 )this.total == 1
            	else this.total -= 1
            },
        	onLoad() {
				this.loading = true;
				setTimeout(() => {
					this.loading = false;
					this.isOk = true;
					setTimeout(() => {
						this.isOk = false;
					}, 1000)
				}, 1000);
			},
        	addToCard() {
            	if(this.islogin) {
            		this.onLoad();
            		const body = querystring.stringify({ product_id: this.product._id, number: this.total});
            		fetch(Vue.config.apiUrl+ 'cart', {
						method :'post',
						headers: {
							'Content-Type' : 'application/x-www-form-urlencoded',
							token          : this.token
			  			},
			  			body : body
					})
					.then(respone => respone.json())
					.then(result => {
						if(!result.status) return this.ShowLogin();
						this.AddCart({
							total : this.total,
							data  : this.product
						});
					})
					.catch(err => alert(''))
            	} else this.ShowLogin();
            },
		},
		computed : {
			...mapState({
				lang    : state => state.Language.lang,
				islogin : state => state.User.isLogin,
				token   : state => state.User.token,
				toLabels() {
					let str = '';
					this.labels.map(label => {
						str += label[this.lang] + ', ';
					})
					str = str.substring(0,str.length - 2);
					return str;
				},
				toSart() {
					if(this.product.evaluate.star) return parseInt(this.product.evaluate.star.toFixed(0))
					else return 4;
				}
			})
		}
	}



