    import {mapState,mapActions} from 'vuex';
	export default{
		data() {
			return {
        	}
        },
        computed : mapState({
			token : state => state.User.token,
			user  : state => {
				if(!state.User.user) return {};
				if(typeof state.User.user == 'string') return JSON.parse(state.User.user);
				return state.User.user
			}
        }),
		methods:{
			 ...mapActions([
                'Logout',
            ]),
			logout() {
				this.Logout();
				this.$router.replace({path : '/'})
			}
	    }
	}



