import Vue                    from 'vue';
import {mapActions, mapState} from 'vuex';
import querystring            from 'querystring';
import Cookie                 from 'vue-cookie';

export default{
	name : 'sign-in',
	data() {
		return {
			islogin   : true,
			login: {
				name     : '',
				password : '',
				example  : true
			},
			register : {
				name       : '',
				email      : '',
				phone      : '',
				password   : '',
				v_password : ''
			}
		}
	},
	methods : {
		close() {
			this.$store.dispatch('HideLogin');
		},
		onLogin() {
			const {name, password} = this.login;
			if(name == ''){ 
				alert("");
				return;
			}
			if(!(/^(\w){6,20}$/.test(password))){ 
				alert("6-20");
				return;
			}
			
			const body = querystring.stringify({key: name, password});
			fetch(Vue.config.apiUrl+'user/login',{
				method  : 'post',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				body : body
			})
			.then(response => response.json())
			.then(result => {
				if(result.status) {
					this.$store.dispatch('Login', {user: result.user, token: result.token});
					// 
					if(this.login.example) {
						Cookie.set('user-token', result.token)
						Cookie.set('user', JSON.stringify(result.user))
					} else {
						Cookie.set('user-token', result.token, { expires: '1h' })
						Cookie.set('user', JSON.stringify(result.user), { expires: '1h' })
					}
				} else alert('')
			})
			.catch(err => alert(''));
		}
	},
    computed : mapState({
        show : state => state.User.showFrom
    })
}



