	import {mapState}  from 'vuex';
	import SubscribeItem from './SubscribeItem';
	export default {
	  	name: 'subscribe',
	  	data () {
	    	return {
	    	}
	  	},
		components : {
			'v-subitem' : SubscribeItem
		},
	  	computed : {
			...mapState({
				banner(state) {
					const banners = state.Info.banners.filter(val => val.key == 'Subscribe-Box-Banner');
					if(banners.length > 0) return banners[0];
					else return {};
	        	}
            })
        },
        watch: {
        	'$route' : function() {
        		console.log('asdsadsa')
        	}
        }
	}



