import * as types from '../mutation-types'

// initial state
const state = {
	count       : 0,
	items       : [],
	select      : [],
	select_gift : []
}

const actions = {
	Initialize({commit}, data) {
		commit(types.CART_INITIALIZE, data);
	},
	AddCart({commit}, data) {
		commit(types.CART_COUNT, data);
	},
	DecrementCart({commit}, data) {
		commit(types.CART_DECR, data);
	},
	InitCart({commit}, data) {
		commit(types.CART_INIT, data);
	},
	InitCartItems({commit}, data) {
		commit(types.CART_ITEMS, data);
	},
	SetCartSelect({commit}, data) {
		commit(types.CART_SELECT, data);
	}
}

// mutations
const mutations = {
	[types.CART_INITIALIZE] (state, data) {
		state.count = 0;
		state.items = [];
	},
	[types.CART_ITEMS] (state, data) {
		state.items = data.items;
	},
	[types.CART_COUNT] (state, data) {
		state.count += data.total;
	},
	[types.CART_DECR] (state, data) {
		state.count -= data.total;
	},
	[types.CART_INIT] (state, data) {
		state.count = data.count;
	},
	[types.CART_SELECT] (state, data) {
		state.select      = [].concat(data.select);
		state.select_gift = [].concat(data.gifts);
	}
}

export default {
	state,
	actions,
	mutations
}


