import VDistpicker from 'v-distpicker';

export default {
    name       : 'area',
    components : {
        'v-distpicker' : VDistpicker
    },
    data () {
        return {
        }
    },
    props : {
        onSelected : {
            type    : Function,
            default : data => {}
        },
        province: {
            type    : String,
            default : ''
        },
        city: {
            type    : String,
            default : ''
        },
        area: {
            type    : String,
            default : ''
        },
        onProvince : {
            type    : Function,
            default : (value) => {
                console.log('onProvince')
            }
        },
        onCity: {
            type    : Function,
            default : (value) => {
                console.log('onCity')
            }
        },
        onArea: {
            type    : Function,
            default : (value) => {
                console.log('onArea')
            }
        }
    }
}



