// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue          from 'vue';
import App          from './App';
import store        from './store';
import router       from './router';
import Cookie       from 'vue-cookie';
import VueSocketio  from 'vue-socket.io';
import ToggleButton from 'vue-js-toggle-button';
import Navbar       from '@/components/navbar';
import Footer       from '@/components/footer';
// import VueI18n   from 'vue-i18n';
import vuexI18n     from 'vuex-i18n';
import vueScroll    from 'vue-scroll';
import Mint         from 'mint-ui';
import VueSweetalert2 from 'vue-sweetalert2';
import VueLazyload from 'vue-lazyload'


Vue.use(Cookie)
// Vue.use(VueI18n)
Vue.use(ToggleButton)
Vue.use(vueScroll) 
Vue.use(Mint)
// register globally
Vue.use(require('vue-infinite-scroll'))
Vue.use(VueSweetalert2);
// 
Vue.use(VueLazyload);


Vue.config.productionTip = false


Vue.component(Mint.Tabbar.name, Mint.Tabbar);
Vue.component(Mint.TabItem.name, Mint.TabItem);
Vue.component('v-navbar', Navbar)
Vue.component('v-footer', Footer)

console.log('NODE_ENV', process.env.NODE_ENV)
if(process.env.NODE_ENV === 'production') {
	Vue.use(VueSocketio, 'http://server.eatisco.com');
	Vue.config.apiUrl = 'http://server.eatisco.com/';
	
	// Vue.use(VueSocketio, 'http://kain.ngrok.cc');
	// Vue.config.apiUrl = "http://kain.ngrok.cc/";
} else {
	Vue.use(VueSocketio, 'http://server.eatisco.com');
	Vue.config.apiUrl = 'http://server.eatisco.com/';
	// Vue.use(VueSocketio, 'http://localhost:8081');
	// Vue.config.apiUrl = "http://localhost:8081/";
}

if(!Cookie.get('lang')) {
	Cookie.set('lang', 'zh')
}

var locale = Cookie.get('lang');

// const i18n = new VueI18n({
// 	locale : locale, // set locale
// 	message: {} // set locale messages
// });

Vue.use(vuexI18n.plugin, store);
Vue.i18n.add('zh', require('@/languages/zh.json'));
Vue.i18n.add('en', require('@/languages/en.json'));
Vue.i18n.set(locale);


/* eslint-disable no-new */
new Vue({
	el         : '#app',
	// i18n,
	store,
	router,
	template   : '<App/>',
	components : { App }
})



