import Vue                    from 'vue';
import {mapState, mapActions} from 'vuex';
import querystring            from 'querystring';

	export default {
		data() {
			return {
				isShowing : false,
				total     : 1,
				loading   : false,
				isOk      : false
			}
		},
		props : {
			data : {
				type    : Object,
				default : {}
			},
			index : {
				type    : Number,
				default : 0
			}
		},
		computed : mapState({
			collects : state => state.User.collects,
			islogin  : state => state.User.isLogin,
			token    : state => state.User.token,
			lang     : state => state.Language.lang,
			active(state){
				return state.User.collects.findIndex(v => v == this.data._id) > -1;
			},
			getImg() {
				if(typeof this.data.img == 'string') return this.data.img
				else return this.data.img[0]
			},
			toSart() {
				if(this.data.evaluate.star) return parseInt(this.data.evaluate.star.toFixed(0))
				else return 4;
			}
		}),
		methods : {
			...mapActions([
				'AddCart',
				'ShowLogin',
				'SetCollect',
				'InitCollects'
			]),
			increment() {
            	this.total += 1;
            },
            decrement() {
            	if (this.total == 1 ) return;
            	else this.total -= 1
            },
        	onLoad() {
				this.loading = true;
				setTimeout(() => {
					this.loading = false;
					this.isOk = true;
					setTimeout(() => {
						this.isOk = false;
					}, 1000)
				}, 1000);
			},
            addToCard() {
            	if(this.islogin) {
            		this.onLoad();
            		const body = querystring.stringify({ product_id: this.data._id, number: this.total});
            		fetch(Vue.config.apiUrl+ 'cart', {
						method :'post',
						headers: {
							'Content-Type' : 'application/x-www-form-urlencoded',
							token          : this.token
			  			},
			  			body : body
					})
					.then(respone => respone.json())
					.then(result => {
						if(!result.status) return this.ShowLogin();
						this.AddCart({
							total : this.total,
							data  : this.data
						});
					})
					.catch(err => alert(''))
            	} else this.ShowLogin();
            },
            collect() {
            	if(this.islogin) {
            		this.SetCollect(this.data._id);

            		const body = querystring.stringify({ collects: this.collects});
            		fetch(Vue.config.apiUrl+ 'user/collection', {
						method :'post',
						headers: {
							'Content-Type' : 'application/x-www-form-urlencoded',
							token          : this.token
			  			},
			  			body : body
					})
					.then(respone => respone.json())
					.then(result => {
						if(!result.status) return;
						this.InitCollects(result.data);
					})
					.catch(err => alert(''))
            	} else this.ShowLogin();
            }
		}
	}



