
import Swiper     from '@/components/swiper';
import Vue        from 'vue';
import ListBox    from './cart.item';
import {mapState,mapActions} from 'vuex';
import querystring from 'querystring';
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'



export default {
name       : 'cart',
components : {
'v-swiper' : Swiper,
'list-box' : ListBox,
GroupTitle,
Swipeout
},
data () {
return {
items       : [],
selects     : [],
select_gift : [],
isEdit      : false
}
},
computed : {
...mapState({
count   : state => state.Cart.count,
token   : state => state.User.token,
islogin : state => state.User.isLogin,
lang    : state => state.Language.lang,
}),
disable() {
return !this.items.length > 0
},
cartTotal() {
var carttotal = 0;

this.items.filter(v => !v.gift).map(val => {
const checked = this.selects.find(v => v==val._id)
if(checked)
carttotal += val.total;
return val;
})
return carttotal;
},
gift_total() {
var gifttotal = 0;
this.items.filter(v => v.gift).map(val => {
const checked = this.select_gift.find(v => v==val._id)
if(checked)
gifttotal += val.total;
return val;
})
return gifttotal;
},
subs() {
return this.items.filter(v => !v.gift);
},
gifts() {
return this.items.filter(v => v.gift);
},
showGift() {
return this.gifts.length > 0;
}
},
watch : {
'lang' : function(val) {
this.items = this.items.map(item => {
item.name = item.product[val].name;
return item
});
},
'islogin' : function(val) {
if(val == true) {
this.getCart();
}
}
},
methods : {
...mapActions([
'AddCart',
'DecrementCart',
'InitCartItems',
'ShowLogin',
'SetCartSelect'
]),
showLogin() {
this.ShowLogin();
},
onSubmit() {
this.InitCartItems({items: this.items});
this.$router.replace({path : 'info', query: { is_gift: false }})
},
onSubmitGift() {
this.InitCartItems({items: this.items});
this.$router.replace({path : 'info', query: { is_gift: true }})
},
onDecrement(item_id) {
let index = 0;
let cart_item = this.items.find((val, i) => {
if(val._id == item_id) {
index = i;
return true;
} else return false;
});

if(cart_item.number == 1) return;
cart_item.number -= 1;
cart_item.total -= cart_item.price;
this.items.splice(index, 1, {...cart_item});

this.DecrementCart({total : 1})

let url = Vue.config.apiUrl+ 'cart';
if(cart_item.is_snackpack) url = Vue.config.apiUrl+ 'cart/snackpack';

const body = querystring.stringify({operation: 'subtract', item_id: cart_item._id});
fetch(url, {
method :'put',
headers: {
'Content-Type' : 'application/x-www-form-urlencoded',
token          : this.token
},
body : body
})
.then(result => {})
},
onIncrement(item_id) {
let index = 0;
let cart_item = this.items.find((val, i) => {
if(val._id == item_id) {
index = i;
return true;
} else return false;
});
cart_item.number += 1;
cart_item.total += cart_item.price;
this.items.splice(index, 1, {...cart_item});

this.AddCart({total: 1})

let url = Vue.config.apiUrl+ 'cart';
if(cart_item.is_snackpack) url = Vue.config.apiUrl+ 'cart/snackpack';

const body = querystring.stringify({operation: 'add', item_id: cart_item._id});
fetch(url, {
method :'put',
headers: {
'Content-Type' : 'application/x-www-form-urlencoded',
token          : this.token
},
body : body
})
.then(result => console.log('success'))
},
onDelete(item_id) {
let index = 0;
const cart_item = this.items.find((val, i) => {
if(val._id == item_id) {
index = i;
return true;
} else return false;
});
this.DecrementCart({total : cart_item.number})
this.items.splice(index, 1);

let url = Vue.config.apiUrl+ 'cart';
if(cart_item.is_snackpack) url += '/snackpack';

const body = querystring.stringify({_id: cart_item._id});
fetch(url, {
method :'delete',
headers: {
'Content-Type' : 'application/x-www-form-urlencoded',
token          : this.token
},
body : body
})
.then(result => {
this.getCart();
this.onLoadCart();
})
},
onSelected(result) {
if(result.selected)
this.selects.push(result.id);
else {
const index = this.selects.findIndex(val => val == result.id);
if(index > -1)
this.selects.splice(index, 1);
}
this.SetCartSelect({select : this.selects, gifts: this.select_gift});
},
onSelectedGift(result) {
if(result.selected)
this.select_gift.push(result.id);
else {
const index = this.select_gift.findIndex(val => val == result.id);
if(index > -1)
this.select_gift.splice(index, 1);
}
this.SetCartSelect({select : this.selects, gifts: this.select_gift});
},
onLoadCart() {
fetch(Vue.config.apiUrl+ 'cart/count', {
method :'get',
headers: {
'Content-Type' : 'application/x-www-form-urlencoded',
token          : this.token
}
})
.then(respone => respone.json())
.then(result => {
if(result.status) this.InitCart({count : result.data.count});
else console.log('');
})
.catch(err => console.log(''))
},
getCart() {
this.items = [];
fetch(Vue.config.apiUrl+ 'cart', {
method :'get',
headers: {
'Content-Type' : 'application/x-www-form-urlencoded',
token          : this.token
}
})
.then(respone => respone.json())
.then(result => {
if(result.status) {

result.cart.cart_item.map(val => {
if(!val.product) return;
this.total += val.number * val.product.price;
this.selects.push(val._id);
this.items.push({
number       : val.number,
product      : val.product,
name         : val.product[this.lang].name,
price        : val.product.price,
_id          : val._id,
total        : val.number * val.product.price,
is_snackpack : false
})
})
result.cart.snackpack_item.map(val => {
if(!val.snackpack) return;
this.total += val.number * val.select.total;
if(val.gift == true) this.select_gift.push(val._id);
else this.selects.push(val._id);
this.items.push({
number       : val.number,
product      : val.snackpack,
name         : val.snackpack[this.lang].name,
price        : val.select.total,
_id          : val._id,
total        : val.number * val.select.total,
is_snackpack : true,
gift         : val.gift
})
})

this.SetCartSelect({select : this.selects, gifts: this.select_gift});
}
else console.log('');
})
.catch(err => console.log('', err))
}
},
beforeMount() {
this.getCart();
}
}




