	import Vue     from 'vue';
	import Healthy from '@/components/Healthy';
	import {
		mapState,
		mapActions
	} from 'vuex';

	Vue.component('sub-item',{
		template : '<div class="column subBox">\
			<div class="ui segment box" @click="onBack" :style="\'background-color: \'+ data.color +\' ;\'">\
	        	<h3>{{data[lang].name}}</h3>\
	        	<i class="fa fa-plus"></i>\
        	</div>\
        </div>',
		data(){
			return {
			}
		},
		props : {
			data : {
				type    : Object,
				default : ''
			},
			back : {
				type    : Function,
				default : (val) => {
					console.log('back', val);
				}
			}
		},
		methods : {
			onBack() {
				this.back(this.data);
			}
		},
	  	computed : {
			...mapState({
				lang : state => state.Language.lang,
			})
		},
	})


	export default {
	  	name: 'subscribebox',
	  	components : {
	  		'v-healthy' : Healthy
	  	},
	  	data () {
	    	return {
				control    : false,
				// items : []
	    	}
	  	},
	  	props : {
			gift : {
				type    : Boolean,
				default : false
			}
	  	},
	  	methods : {
	  		...mapActions(['InitSubscribes']),
	  		onBack(val) {
	  			this.$refs.healthy.data = val;
	  			this.control = true;
	  		},
	  		close() {
	  			this.control = false;
	  		},
            getList() {
            	
	        	fetch(Vue.config.apiUrl+'snackpack', {
					method :'get'
	        	})
	        	.then(respone => respone.json())
	        	.then(result => this.InitSubscribes({subscribes: result.data, lang: this.$i18n.locale()}))
	        	.catch(err => console.log('err', err))
            },
	  	},
	  	computed : {
			...mapState({
				subscribes : state => state.Subscribe.subscribes,
				lang       : state => state.Language.lang,
				items(state) {
					let i = 0;
					const bgcolor = new Array('rgba(222,78,173,.8)','rgba(254,236,80,.8)','rgba(73,223,214,.8)')
					if(state.Subscribe.subscribes.length > 0)
						return state.Subscribe.subscribes.map(val => {
							if(bgcolor.length > i) {
								val.color = bgcolor[i];
								i++;
							} else val.color = bgcolor[0];
							return val;
						});
					else return state.Subscribe.subscribes;
				}
			})
		},
	  	created() {
			this.getList();
		}
	}



