import Vue from 'vue';
import moment from 'moment';
import {mapState,mapActions} from 'vuex';
export default{
	data() {
		return{
			list : []
		}
	},
	computed : mapState({
		token : state => state.User.token,
		lang : state => state.Language.lang,
    }),
    methods : {
		moment() {
			return moment().format('MM/DD/YYYY');
		},
		status(statu) {
			switch(statu) {
				case 0:
					return 'notpaying';
				case 1:
					return 'waiting';
				case 2:
					return 'received';
			}
		}
	},
	created() {
		const { token } = this;
	  	// const body = querystring.stringify({ username: account, password});
  		fetch(Vue.config.apiUrl+'order/user/list',{
			method  : 'GET',
			headers : {
				'Content-Type' : 'application/x-www-form-urlencoded',
				token
			}
  		})
  		.then(response => response.json())
  		.then(result => {
  			console.log(result)
  			if(result.status) this.list = result.orders;
  			else console.log('')
  		})
  		.catch(err => console.error(err));
	}
}
	



