

import {mapState,mapActions} from 'vuex';
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
data() {
return{
checked : true,
}
},
components : {
SwipeoutItem,
SwipeoutButton,
},
watch : {
checked(val) {
this.onSelected({id:this.data._id, selected:val})
}
},
props : {
index        : Number,
data         : {
type : Object,
default : () => {
return {}
}
},
isEdit : false,
onRemove    : {
type    : Function,
default : () => console.log('remove')
},
onIncrement : {
type    : Function,
default : () => console.log('increment')
},
onDecrement : {
type    : Function,
default : () => console.log('decrement')
},
onSelected : {
type    : Function,
default : () => console.log('selected')
}
},
computed : mapState({
lang : state => state.Language.lang
}),
methods : {
increment() {
this.onIncrement(this.data._id);
},
decrement() {
this.onDecrement(this.data._id);
},
remove() {
this.onRemove(this.data._id);
}
}
}



