export default {
    data() {
      	return {
        	swiperOption: {
          		nextButton: '.swiper-button-next',
          		prevButton: '.swiper-button-prev',
          		slidesPerView: 3, //
          		centeredSlides: false,
	            paginationClickable: true,
	            spaceBetween: 20 //margin
        	}
      	}
    },
    props : {
    	swiperSlides : {
    		type : Array,
    		default : () => {
    			return [
		    		{
		    			name : '1',
		    			img : ''
		    		},{
		    			name : '2',
		    			img : ''
		    		},{
		    			name : '3',
		    			img : ''
		    		},{
		    			name : '4',
		    			img : ''
		    		},{
		    			name : '5',
		    			img : ''
		    		},{
		    			name : '6',
		    			img : ''
		    		},{
		    			name : '7',
		    			img : ''
		    		}]
    		}
    	}
    },
    methods: {
      	appendSlide() {
        	this.swiperSlides.push(this.swiperSlides.length + 1)
      	},
      	prependSlide() {
        	this.swiperSlides.unshift(this.swiperSlides[0] - 1)
      	}
    }
}



