    import {mapState,mapActions} from 'vuex';
	import defArticle    from '../def_article';

    export default {
        name: 'about',
        data () {
            return {
            }
        },
        methods : {
        },
        computed : {
			...mapState({
				articles : state => state.Info.articles,
				lang     : state => state.Language.lang,
				banner(state) {
					const banners = state.Info.banners.filter(val => val.key == 'About-Banner1');
					if(banners.length > 0) return banners[0];
					else return {};
	        	}
            }),
            article1() {
                if(this.articles.length > 0){
                	const article = this.articles.find(val => val.key == 'About1');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            },
            article2() {
                if(this.articles.length > 0){
                    const article = this.articles.find(val => val.key == 'About2');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            },
            article3() {
            	if(this.articles.length > 0){
                    const article = this.articles.find(val => val.key == 'About3');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            },
            article4() {
            	if(this.articles.length > 0){
                    const article = this.articles.find(val => val.key == 'About4');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            }
        }
    }



