import * as types  from '../mutation-types'
import Vue         from 'vue';
import querystring from 'querystring';
import Cookie      from 'vue-cookie';

// initial state
const state = {
	showFrom : false,
	isLogin  : Cookie.get('user-token') != null,
	user     : Cookie.get('user'),
	token    : Cookie.get('user-token'),
	collects : [],
	address  : {
					first_name : '',
					last_name  : '',
					phone      : '',
					email      : '',
					city       : '',
					district   : '',
					address    : '',
					address2   : '',
					note       : ''
                },
    book : []
}

const actions = {
	InitCollects({commit}, data) {
		commit(types.USER_COLLECT_INIT, data);
	},
	SetCollect({commit}, data) {
		commit(types.USER_COLLECT_SET, data);
	},
	Login({commit}, data) {
		commit(types.USER_LOGIN, data);
	},
	Logout({commit}, data) {
		commit(types.USER_LOGOUT, data);
	},
	ShowLogin({commit}, data) {
		commit(types.USER_LOGIN_SHOW, data);
	},
	HideLogin({commit}, data) {
		commit(types.USER_LOGIN_HIDE, data);
	},
	InitAddress({commit}, data) {
		commit(types.USER_ADDRESS_INIT, data);
	},
	SetBook({commit}, data) {
		commit(types.USER_BOOK_SET, data);
	},
}

// mutations
const mutations = {
	[types.USER_COLLECT_SET] (state, data) {
		const index = state.collects.findIndex(v => v == data);
		if(index > -1) state.collects.splice(index, 1);
		else state.collects.push(data);
	},
	[types.USER_COLLECT_INIT] (state, data) {
		if(data) state.collects = data;
		else state.collects = [];
	},
	[types.USER_LOGIN] (state, data) {
		state.user     = data.user;
		state.token    = data.token;
		state.isLogin  = true;
		state.showFrom = false;
	},
	[types.USER_LOGOUT] (state, data) {
		state.isLogin = false;
		state.token   = '';
		state.user    = {};
	},
	[types.USER_LOGIN_SHOW] (state, data) {
		state.showFrom = true;
	},
	[types.USER_LOGIN_HIDE] (state, data) {
		state.showFrom = false;
	},
	[types.USER_ADDRESS_INIT] (state, data) {
		state.address = data;
	},
	[types.USER_BOOK_SET] (state, data) {
		state.book = data;
	}
}

export default {
	state,
	actions,
	mutations
}


