import * as types from '../mutation-types'

// initial state
const state = {
	org_products : [],
	products     : [],
	filters      : [],
	filter       : [],
	sort         : ''
}

const actions = {
	InitProduct({commit}, data) {
		commit(types.PRODUCT_INIT, data);
	},
	SetProduct({commit}, data) {
		commit(types.PRODUCT_SET, data);
	},
	SetFilters({commit}, data) {
		commit(types.PRODUCT_FILTER, data);
	},
	UpdateProduct({commit}, data) {
		commit(types.PRODUCT_UPDATE, data);
	},
	FilterProduct({commit}, data) {
		commit(types.FILTER_PRODUCT, data);
	},
	SortProduct({commit}, data) {
		commit(types.SORT_PRODUCT, data);
	}
}

// mutations
const mutations = {
	[types.PRODUCT_INIT] (state, data) {
		const products = data.products.map(product => {
			if(product[data.lang]) {
				product.name = product[data.lang].name;
	            product.desc = product[data.lang].desc;
	            product.content = product[data.lang].content;
			}
            return product;
        });
		state.products = products;
        state.org_products = products;
	},
	[types.PRODUCT_SET] (state, data) {
		const products = state.products.concat(data.products);
		state.products = products;
        state.org_products = products;
	},
	[types.PRODUCT_UPDATE] (state, data) {
        state.products = state.products;
        state.org_products = state.products;

        state.filters = state.filters;
        
	},
	[types.PRODUCT_FILTER] (state, data) {
		state.filters = data.filters
	},
	[types.FILTER_PRODUCT] (state, data) {
		if(data.length > 0)
			state.products = state.org_products.filter(val => {
				let select = false;
				data.forEach(item => {
					if(val.labels.includes(item._id)) select = true;
				})
				return select
			})
		else 
			state.products = state.org_products;
	},	
	[types.SORT_PRODUCT] (state, data) {
		if(data.field == 'price') {
			if(data.sort > 0) 
				state.products.sort((a, b) => a.price - b.price)
			else
				state.products.sort((a, b) => b.price - a.price)
		} else if(data.field == 'popularity') {

		}
	},
}

export default {
	state,
	actions,
	mutations
}


