	import Vue                    from 'vue';
	import {mapState, mapActions} from 'vuex';
	import querystring            from 'querystring';

	Vue.component('item-box', {
		template: '\
			<div class="items" style="font-size: 12px;">\
				<img v-lazy="data.product.img[0]"/>\
				<div class="itemTitle" style="overflow: hidden; text-overflow:ellipsis;">\
					{{data.product[lang].name}}\
				</div>\
				<p style="width: 100px; display: inline-block; overflow: hidden;text-overflow:ellipsis;">{{data.product[lang].desc}}</p>\
			</div>\
		',
		props : {
			data : {}
		},
		computed : mapState({
			lang : state => state.Language.lang
        }),
	})

	export default{
		name : 'healthy',
		data() {
			return {
				data     : {
					items  : [],
					select : []
				},
				selected : 0,
				loading  : false,
				isOk     : false
			}
		},
		props : {
			show : false,
			gift : false,
			close : {
				type    : Function
			}
		},
		computed : mapState({
            token   : state => state.User.token,
            islogin  : state => state.User.isLogin,
			lang    : state => state.Language.lang
        }),
		methods : {
			...mapActions([
				'AddCart',
				'ShowLogin',
			]),
			onClose() {
				this.close();
			},
			onLoad() {
				this.loading = true;
				setTimeout(() => {
					this.loading = false;
					this.isOk = true;
					setTimeout(() => {
						this.isOk = false;
					}, 1000)
				}, 1000);
			},
			AddToCart() {
				if(this.islogin) {
					this.onLoad();
					const snackpack = this.data.select[this.selected];
	                const body = querystring.stringify({_id: this.data._id , select: JSON.stringify(snackpack), gift : this.gift});
	                fetch(Vue.config.apiUrl+ 'cart/snackpack', {
	                    method :'post',
	                    headers: {
	                        'Content-Type' : 'application/x-www-form-urlencoded',
	                        token          : this.token
	                    },
	                    body : body
	                })
	                .then(result => this.AddCart({total : 1}))
	            } else this.ShowLogin();

			}
		} 
	}



