import Vue                    from 'vue';
import {mapState, mapActions} from 'vuex';
import querystring            from 'querystring';

	export default {
		data() {
			return {
				code : ''
			}
		},
		computed : mapState({
			islogin  : state => state.User.isLogin,
			token    : state => state.User.token
		}),
		methods : {
			getCode() {
				alert('code:' + this.$router.query);
				console.log('code', this.$router.query)
			}
		},
		created() {
			this.getCode();
		}
	}



