	import Vue                    from 'vue';
	import {mapState, mapActions} from 'vuex';
	import CollectItem            from './CollectItem';

	export default{
		name : 'collect',
		data() {
			return {
				collects : []
			}
		},
		components : {
			'collect-item' : CollectItem,
        },
		methods: {
		},
		computed : {
			...mapState({
				token   : state => state.User.token
			})
		},
		created() {
			fetch(Vue.config.apiUrl+ 'collects', {
				method :'get',
				headers: {
					'Content-Type' : 'application/x-www-form-urlencoded',
					token          : this.token
				}
			})
			.then(respone => respone.json())
			.then(result => {
				if(!result.status) return this.ShowLogin();

				this.collects = result.products;
			})
			.catch(err => alert(''))
		}
	}



