	import Vue                    from 'vue';
	import {mapState, mapActions} from 'vuex';
	import Cookie                 from 'vue-cookie';

    export default {
        name: 'navbar',
        data () {
            return {
				show       : false,
				active     : false,
				giftInfo   : false, //
				searchMore : '',
				language   : this.$i18n.locale() == 'en'? true : false
            }
        },
        methods : {
        	...mapActions([
                'UpdateArticle',
                'UpdateProduct',
                'SetLanguage',
                'InitProduct',
                'UpdateSubscribes'
            ]),
            change() {
                this.show   = !this.show
                this.active = !this.active
            },
            langChange() {
            	if(this.lang == 'en') {
            		this.$i18n.set('zh');
					this.$cookie.set('lang', 'zh');
					this.SetLanguage('zh');
				} else {
					this.$i18n.set('en');
					this.$cookie.set('lang', 'en');
					this.SetLanguage('en');
				}
            },
            search() {
                // this.searchMore = item;
                // this.products.find({})

                // this.$router.push({path:'/shop/product', query : { _id: item.value, index: item.index}})
            }
        },
        computed : mapState({
			count    : state => state.Cart.count,
			products : state => state.Product.products,
			lang     : state => state.Language.lang
        }),
        mounted() {
        }
    }



