	import Swiper                 from '@/components/swiper';
	import Swipermore             from '@/components/swiper-more';
	import Subscribebox           from './Subscribebox';
	import {mapState, mapActions} from 'vuex';
	import defArticle    from '../def_article';

	export default {
	  	name: 'subscribe',
	  	components : {
			'v-swiper'     : Swiper,
			'v-swipermore' : Swipermore,
			'v-subbox'     : Subscribebox
	  	},
	  	data () {
	    	return {
	    	}
	  	},
        computed : {
			...mapState({
				lang : state => state.Language.lang,
				banner(state) {
					const banners = state.Info.banners.filter(val => val.key == 'Subscribe-Banner1');
					return banners.map(val => {
						return val.img
					})
	        	},
	        	articles : state => state.Info.articles
            }),
            article1() {
                if(this.articles.length > 0){
                	const article = this.articles.find(val => val.key == 'Subscribe1');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            },
            article2() {
                if(this.articles.length > 0){
                	const article = this.articles.find(val => val.key == 'Subscribe2');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            },
            article3() {
                if(this.articles.length > 0){
                	const article = this.articles.find(val => val.key == 'Subscribe3');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            }
        }
	}



