	import Vue                    from 'vue';
	import querystring            from 'querystring';
	import {mapState, mapActions} from 'vuex';
	import Swiper                 from '@/components/swiper-fade';
	import Swipermore             from '@/components/swiper-more';


	export default{
		name : 'product',
		components : {
			'v-swipermore' : Swipermore,
			'v-swiper'     : Swiper
        },
		data() {
			return {
				total           : 1,
				loading         : false,
				isOk            : false,
				showComposition : false,
				showPartner     : false,
				showEval        : false
			}
		},
		methods : {
			...mapActions([
				'AddCart',
				'ShowLogin',
				'InitProduct',
				'SetCollect',
				'InitCollects'
			]),
			back() {
				this.$router.go(-1)
			},
			increment() {
            	this.total += 1
            },
            decrement() {
            	if (this.total < 1 )this.total == 1
            	else this.total -= 1
            },
        	onLoad() {
				this.loading = true;
				setTimeout(() => {
					this.loading = false;
					this.isOk = true;
					setTimeout(() => {
						this.isOk = false;
					}, 1000)
				}, 1000);
			},
            addToCard() {
            	if(this.islogin) {
            		this.onLoad();
            		const body = querystring.stringify({ product_id: this.data._id, number: this.total});
            		fetch(Vue.config.apiUrl+ 'cart', {
						method :'post',
						headers: {
							'Content-Type' : 'application/x-www-form-urlencoded',
							token          : this.token
			  			},
			  			body : body
					})
					.then(respone => respone.json())
					.then(result => {
						if(!result.status) return this.ShowLogin();
						this.AddCart({
							total : this.total,
							data  : this.data
						});
					})
					.catch(err => alert(''))
            	} else this.ShowLogin();
            },
            collect() {
            	if(this.islogin) {
            		this.SetCollect(this.data._id);

            		const body = querystring.stringify({ collects: this.collects});
            		fetch(Vue.config.apiUrl+ 'user/collection', {
						method :'post',
						headers: {
							'Content-Type' : 'application/x-www-form-urlencoded',
							token          : this.token
			  			},
			  			body : body
					})
					.then(respone => respone.json())
					.then(result => {
						if(!result.status) return;
						this.InitCollects(result.data);
					})
					.catch(err => alert(''))
            	} else this.ShowLogin();
            },
            featch() {
            	fetch(Vue.config.apiUrl +'product/?per_page=0&page=1000', {
					method :'get'
	        	})
	        	.then(respone => respone.json())
	        	.then(result => {
	        		this.InitProduct({products : result.data});
	        	})
	        	.catch(err => console.log('err', err))
            }
		},
		computed : {
			...mapState({
				collects : state => state.User.collects,
				islogin  : state => state.User.isLogin,
				token    : state => state.User.token,
				products : state => state.Product.products,
				lang     : state => state.Language.lang,
				active(state){
					return state.User.collects.findIndex(v => v == this.data._id) > -1;
				}
			}),
			data() {
				if(this.products.length > 0) return this.products[this.$route.query.index]
                else return {
					name : "************",
					desc : "************",
					content : "************",
					price : 0,
					unit  : '************',
					composition: [{key : 'Sugar content', value: 'no'}]
				}
			}
		}

	}



