	import Vue from 'vue';
    import querystring from 'querystring';
	import {mapState,mapActions} from 'vuex';

	export default{
		name : 'address',
		data() {
			return {
				isAdd   : true
			}
		},
		computed : mapState({
			token       : state => state.User.token,
			lang        : state => state.Language.lang,
			addressList : state => state.User.book
		}),
		methods: {
			...mapActions([
                'SetBook',
            ]),
			editor(data) {
				this.$router.push({path : '/addAddress', query: {...data}})
			},
			del(_id) {
				const { token, SetBook } = this;
				const body = querystring.stringify({_id});
				fetch(Vue.config.apiUrl+'user/address/book',{
					method  : 'DELETE',
					headers : {
						'Content-Type' : 'application/x-www-form-urlencoded',
						token
					},
					body
				})
				.then(response => response.json())
				.then(result => {
					if(result.status) SetBook(result.data);
	  				else console.log('')
				})
				.catch(err => console.error(err));
			}
		},
		created() {
			const { token, SetBook } = this;
			fetch(Vue.config.apiUrl+'user/address/book',{
				method  : 'GET',
				headers : {
					'Content-Type' : 'application/x-www-form-urlencoded',
					token
				}
			})
			.then(response => response.json())
			.then(result => {
				if(result.status) SetBook(result.data);
  				else console.log('')
			})
			.catch(err => console.error(err));
		}
	}



