import Vue    from 'vue';
import {
    mapState,
    mapActions
}             from 'vuex';
import Swiper from '@/components/swiper';
import defArticle    from '../def_article';

export default {
    name: 'home',
    components : {
        'v-swiper' : Swiper
    },
    data () {
        return {
        }
    },
    computed : {
        ...mapState({
            token   : state => state.User.token,
            islogin : state => state.User.isLogin,
            lang    : state => state.Language.lang,
            articles : state => state.Info.articles,
            banner(state) {
                const banners = state.Info.banners.filter(val => val.key == 'Home-Banner1');
                return banners.map(val => {
                    return val.img
                })
            },
            banner2(state) {
                const banner = state.Info.banners.find(val => val.key == 'Home-Banner2');
                if(banner) return banner.img;
            }
        }),
        article1() {
            if(this.articles.length > 0){
                const article = this.articles.find(val => val.key == 'Home1');
                if(article) return article;
                else return defArticle;
            } else return defArticle;
        },
        article2() {
            if(this.articles.length > 0){
                const article = this.articles.find(val => val.key == 'Home2');
                if(article) return article;
                else return defArticle;
            } else return defArticle;
        }
    }
}




