	export default {
	    data() {
	      	return {
		        swiperOption: {
						autoplay            : 2500,
						initialSlide        : 1,
						pagination          : '.swiper-pagination',
						paginationClickable : true,
						nextButton          : '.swiper-button-next',
						prevButton          : '.swiper-button-prev',
						spaceBetween        : 30,
						effect              : 'fade',
						loop                : true
		        }
	      	}
	    },
	    props : {
	    	images : {
				type    : Array,
				default : () => []
	    	}
	    }
    }



