import Vue                    from 'vue';
import {mapState, mapActions} from 'vuex';
import moment                 from 'moment';
import querystring            from 'querystring';
import area                   from '@/components/area.vue';
import datetime               from 'vui-datetime-picker';

export default {
    name: 'info',
    data () {
        return {
            loading    : true,
            Info       : true, //
            giftInfo   : false, //
            changeType : false, //giftInfo
            address    : {
                first_name : '',
                last_name  : '',
                phone      : '',
                email      : '',
                city       : '',
                district   : '',
                province   : '',
                address    : '',
                address2   : '',
                note       : '',
                remark     : ''
            },
            selectIndex : 0,
            addressbook : [],
            is_gift     : this.$route.query.is_gift
        }
    },
    components : {
        'v-area'            : area,
        'v-datetime-picker' : datetime
    },
    watch : {
        selectIndex : function(val) {
            let address = this.addressbook[val];
            delete address._id;
            this.address = address;
        }
    },
    methods :{
        ...mapActions([
            'InitAddress'
        ]),
        moment(date, fmt = 'YYYY-MM-DD, h:mm:ss') {
            if(date) return moment(date).format(fmt);
            else return moment().format(fmt);
        },
        back() {
            this.$router.replace('cart')
        },
        onSelect(event, val) {
        },
        onProvince(obj) {
            this.address.province = obj.value;
        },
        onCity(obj) {
            this.address.city = obj.value;
        },
        onArea(obj) {
            this.address.district = obj.value;
        },
        changeGift() { //
            this.changeType = !this.changeType
            this.giftInfo   = !this.giftInfo
            this.Info       = !this.Info
        },
        onSubmit() { //,
            if(!this.$route.query.is_gift) this.address.remark = '';
            const body = querystring.stringify({address: JSON.stringify(this.address)});
            fetch(Vue.config.apiUrl+ 'user/address', {
                method :'post',
                headers: {
                    'Content-Type' : 'application/x-www-form-urlencoded',
                    token          : this.token
                },
                body:body
            })
            .then(respone => respone.json())
            .then(result => this.InitAddress(result.data))
            .catch(err => console.log(''))

            this.$router.replace({path : 'payment', query: { is_gift: this.$route.query.is_gift }})
        }
    },
    computed : mapState({
        token : state => state.User.token
    }),
    created() {
        fetch(Vue.config.apiUrl+ 'user/address', {
            method :'get',
            headers: {
                'Content-Type' : 'application/x-www-form-urlencoded',
                token          : this.token
            }
        })
        .then(respone => respone.json())
        .then(result => {
            if(result.data) {
                this.address = result.data;
                this.addressbook = result.books;
            }
            this.loading = false;
        })
        .catch(err => alert(''))
    },
}



