	import Vue from 'vue';
    import area from '@/components/area.vue';
    import {mapState,mapActions} from 'vuex';
    import querystring from 'querystring';

    export default{
    	name : 'addAddress',
    	data() {
    		return {
    			data : this.$route.query
    		}
    	},
    	components : {
            'v-area' : area,
        },
        computed : mapState({
			token : state => state.User.token,
        }),
        methods: {
        	...mapActions([
                'SetBook',
            ]),
        	onProvince(obj) {
                this.data.province = obj.value;
            },
            onCity(obj) {
                this.data.city = obj.value;
            },
            onArea(obj) {
                this.data.district = obj.value;
            },
        	save() {
        		const { token, data, SetBook, $router } = this;
				const body   = querystring.stringify(data);
				const method = data._id?'PUT':'POST';

				fetch(Vue.config.apiUrl + 'user/address/book',{
					method  : method,
					headers : {
						'Content-Type' : 'application/x-www-form-urlencoded',
						token
					},
					body
				})
				.then(response => response.json())
				.then(result => {
					if(result.status) {
						SetBook(result.data)
						this.$router.go(-1);
					}
					else console.log('error', result)
					this.hide = false;
				})
				.catch(err => console.error(err));
        	},
        }
    }




