import * as types from '../mutation-types'

// initial state
const state = {
	subscribes :[]
}

const actions = {
	InitSubscribes({commit}, data) {
		commit(types.SUBSCRIBE_INIT, data);
	},
	UpdateSubscribes({commit}, data) {
		commit(types.SUBSCRIBE_UPDATE, data);
	}
}

// mutations
const mutations = {
	[types.SUBSCRIBE_INIT] (state, data) {
		state.subscribes = data.subscribes;
	},
	[types.SUBSCRIBE_UPDATE] (state, data) {
	}
}

export default {
	state,
	actions,
	mutations
}


