import * as types from '../mutation-types'

// initial state
const state = {
	banners  : [],
	articles : []
}

const actions = {
	InitBanner({commit}, data) {
		commit(types.BANNER_INIT, data);
	},
	InitArticle({commit}, data) {
		commit(types.ARTICLE_INIT, data);
	},
	GetBanner({state}, key) {
		const banner = state.banners.filter(val => val.key == key)
		return banner
	},
	UpdateArticle({commit}, data) {
		commit(types.ARTICLE_UPDATE, data);
	}
}

// mutations
const mutations = {
	[types.BANNER_INIT] (state, data) {
		state.banners = data;
	},
	[types.ARTICLE_INIT] (state, data) {
		state.articles = data.articles
	},
	[types.ARTICLE_UPDATE] (state, data) {
		state.articles = state.articles
	}
}

export default {
	state,
	actions,
	mutations
}


