	import {mapState}    from 'vuex';
	import SubscribeItem from './SubscribeItem';
	import defArticle    from '../def_article';

	export default{
		name : 'gift',
		data() {
			return {
				show       : false,
				active     : false,
				searchMore : ''
			}
		},
        components : {
			'v-subitem' : SubscribeItem
		},
        computed : mapState({
            ...mapState({
            	lang : state => state.Language.lang,
				banner(state) {
					const banners = state.Info.banners.filter(val => val.key == 'Gift-Banner1');
					if(banners.length > 0) return banners[0];
					else return {};
	        	},
	        	articles : state => state.Info.articles
            }),
            article1() {
                if(this.articles.length > 0){
                	const article = this.articles.find(val => val.key == 'Gift1');
                    if(article) return article;
                    else return defArticle;
                } else return defArticle;
            }
        })
	}



