	import Vue          from 'vue';
	import {mapActions, mapState} from 'vuex';
	import querystring  from 'querystring';
	import ImgItem      from './ShopListItem';

	Vue.component('img-item', ImgItem)
	export default{
		name : 'shoplist', 
		data() {
			return{
				page       : 1,
				per_page   : 6,
				all        : 0,
				count      : 0,
				busy       : true,
				SortShow   : false,
				FilterShow : false,
				filter     : [],
				popularity : 0,
				price      : 0
			}
		},
		computed : {
			...mapState({
                lang     : state => state.Language.lang,
                products : state => state.Product.products,
                filters  : state => state.Product.filters
            })
		},
		methods : {
			...mapActions([
				'SetProduct',
				'FilterProduct',
				'SortProduct',
                'InitProduct'
			]),
			FilterSelected() {
                this.FilterShow = !this.FilterShow;
                this.SortShow   = false;
            },
            SortSelected() {
                this.SortShow   = !this.SortShow;
                this.FilterShow = false;
            },
            onFilter(tab) {
                const index = this.filter.findIndex(val => val == tab);
                if(index == -1) this.filter.push(tab);
                else this.filter.splice(index, 1);
                this.FilterProduct(this.filter);
            },
             onAllFilter() {
                this.filter = [];
                this.FilterProduct(this.filter);
            },
            selectCss(tab) {
                return this.filter.find(val => val == tab);
            },
            onSortPrice() {
                this.popularity = 0;
                if(this.price == 0) this.price = 1;
                else if(this.price == 1) this.price = -1;
                else if(this.price == -1) this.price = 1;
                this.SortProduct({field: 'price', sort : this.price})
            },
            onSortPopularity() {
                this.price = 0;
                if(this.popularity == 0) this.popularity = 1;
                else if(this.popularity == 1) this.popularity = -1;
                else if(this.popularity == -1) this.popularity = 1;
                this.SortProduct({field: 'popularity', sort : this.popularity})
            },
			onScroll(e, position) {
                console.log('')
				this.NextPage()
			},
			getUrl(sort) {
            	let url = Vue.config.apiUrl +'product/?';
            	if(sort) {
            		url += querystring.stringify({sort, page: this.page, per_page: this.per_page});
            	} else {
            		url += querystring.stringify({page: this.page, per_page: this.per_page});
            	}
            	return url;
            },
            getList() {
            	this.busy = true;
            	const url = this.getUrl();
	        	fetch(url, {
					method :'get',
					// headers : {}
	        	})
	        	.then(respone => respone.json())
	        	.then(result => {
	        		this.all = result.total;
	        		this.count += result.data.length;
	        		if(result.data.length > 0 ){
                        this.SetProduct({products : result.data, lang: this.lang});
                        this.busy = false;
                    } 
	        	})
	        	.catch(err => console.log('err', err))
            },
            NextPage() {
                if(!this.busy) {
                    this.page += 1;
                    this.getList();
                }
            }
		},
		created() {
			// this.getList();
            fetch(Vue.config.apiUrl +'product/?per_page='+this.per_page+'&page=' + this.page, {
                method :'get'
            })
            .then(respone => respone.json())
            .then(result => {
                // this.searchData = result.data
                this.InitProduct({products : result.data, lang: this.lang});
                this.busy = false;
            })
            .catch(err => console.log('err', err))
		}
            
	}



