import Vue from 'vue';
import moment from 'moment';
import {mapState,mapActions} from 'vuex';
import querystring from 'querystring';

export default {
    name: 'payment',
    data () {
        return {
            loading  : true,
            payment  : '',
            delivery : 0,
            discount : 100,
            total    : 0,
            freight  : 0
        }  
    },
    methods : {
        ...mapActions([
            'Initialize'
        ]),
        back() {
            this.$router.replace('info')
        },
        onChangePayment(payment) {
            this.payment = payment;
            this.onPlaceOrder();
        },
        
        onPlaceOrder() {
            let body = querystring.stringify({order : this.orderId, total : this.total});

            fetch(Vue.config.apiUrl+ 'payment/wechat', {
                method :'post',
                headers: {
                    'Content-Type' : 'application/x-www-form-urlencoded',
                    token : this.token
                },
                body
            })
            .then(respone => respone.json())
            .then(result => {
                WeixinJSBridge.invoke('getBrandWCPayRequest', result, (res) => {
                    if(res.err_msg == "get_brand_wcpay_request:ok"){
                        this.onSubmitOrder()
                    // 
                    }else{
                        this.$swal({
                            title             : '',
                            type              : 'error',
                            confirmButtonText : ''
                        });
                    }
                });
            })
            .catch(err => {
                this.$swal({
                    title             : '',
                    type              : 'error',
                    confirmButtonText : ''
                });
            })
        },
        onSubmitOrder() {
            let body = querystring.stringify({_id : this.orderId, address: JSON.stringify(this.address), select: this.select});
            if(this.$route.query.is_gift) body = querystring.stringify({_id : this.orderId, address: JSON.stringify(this.address), select: this.gifts});
            fetch(Vue.config.apiUrl+ 'order/wechat/pay', {
                method :'post',
                headers: {
                    'Content-Type' : 'application/x-www-form-urlencoded',
                    token : this.token
                },
                body : body
            })
            .then(respone => respone.json())
            .then(result => {
                this.Initialize();
                this.$router.go(-1)
                // this.$router.replace({path : '/'})
                // alert(this.token);
            })
        }
    },
    computed : mapState({
        items   : state => state.Cart.items,
        token   : state => state.User.token,
        address : state => state.User.address,
        lang    : state => state.Language.lang,
        select  : state => state.Cart.select,
        gifts   : state => state.Cart.select_gift,
        toPay() {
            return this.payment != ''
        },
        filterItem() {
            const is_gift = this.$route.query.is_gift;
            const result = this.items.filter(item => {
                const i = is_gift?this.gifts.find(f => f == item._id) : this.select.find(f => f == item._id);
                if(i) return true;
                else return false;
            });
            return result;
        },
        orderId() {
            var str = "" + moment().unix(),
            pad = "000000000",
            _id = moment().format("YYYY") + moment().format("MM") + pad.substring(0, pad.length - str.length) + str;
            return _id;
        }
    }),
    created() {
        if(this.select.length == 0 && this.gifts.length == 0) {
            this.$router.replace('cart');
            return;
        }
        let body = querystring.stringify({select: this.select});
        if(this.$route.query.is_gift) body = querystring.stringify({select: this.gifts});
        fetch(Vue.config.apiUrl+ 'order/test/place', {
            method :'post',
            headers: {
                'Content-Type' : 'application/x-www-form-urlencoded',
                token          : this.token
            },
            body : body
        })
        .then(respone => respone.json())
        .then(result => {
            const data = result.data;
            this.delivery = (data.original_total - data.total).toFixed(1);
            this.total    = data.total.toFixed(1);
            this.discount = data.discount;
            this.freight = data.freight;
            this.loading = false;
        })
        .catch(err => console.log(''))
    }
}



