import login      from '@/components/Sign-in';
import Vue        from 'vue';
import Cookie from 'vue-cookie';
import {mapState,mapActions} from 'vuex';

export default {
  	name: 'app',
    data() {
        return {
            selected: 'shop'
        }
    },
    watch: {
        // 
        '$route': 'reloadMune',
        selected(val) {
            switch(val) {
                case 'shop':
                    this.$router.push({path : '/shop'})
                break;
                case 'subscribe':
                    this.$router.push({path : '/sub'})
                break;
                case 'gift':
                    this.$router.push({path : '/gift'})
                break;
                case 'cart':
                    this.$router.push({path : '/cart'})
                break;
                case 'me':
                    this.$router.push({path : '/me'})
                break;
            }
        }
    },
    components : {
        'v-login' : login
    },
    computed : mapState({
        count    : state => state.Cart.count,
        token   : state => state.User.token,
        islogin : state => state.User.isLogin,
        lang    : state => state.Language.lang,
    }),
    methods : {
        ...mapActions([
          'InitCart',
          'InitCollects',
          'InitBanner',
          'InitArticle',
          'SetFilters',
          'SetLanguage'
        ]),
        reloadMune() {
            const URL = this.$route.fullPath;
            switch(URL) {
                case '/':
                    this.selected = 'shop';
                break;
                case '/shop':
                    this.selected = 'shop';
                break;
                case '/sub':
                    this.selected = 'subscribe';
                break;
                case '/gift':
                    this.selected = 'gift';
                break;
                case '/cart':
                    this.selected = 'cart';
                break;
                case '/me':
                    this.selected = 'me';
                break;
            }
        },
        onLoadCart() {
            fetch(Vue.config.apiUrl+ 'cart/count', {
                method :'get',
                headers: {
                    'Content-Type' : 'application/x-www-form-urlencoded',
                    token          : this.token
                }
            })
            .then(respone => respone.json())
            .then(result => {
                if(result.status) this.InitCart({count : result.data.count});
                else console.log('');
            })
            .catch(err => console.log(''))
        },
        onLoadCollection() {
            fetch(Vue.config.apiUrl+ 'user/collection', {
                method :'get',
                headers: {
                    'Content-Type' : 'application/x-www-form-urlencoded',
                    token          : this.token
                }
            })
            .then(respone => respone.json())
            .then(result => {
                if(result.status) this.InitCollects(result.data);
                else console.log('');
            })
            .catch(err => console.log(''))
        }
    },
    beforeCreate() {

        //url
        function getUrlParam(name) {
            const reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
            const r = window.location.search.substr(1).match(reg);
            if (r != null) return unescape(r[2]); return null;
        }
        const code = getUrlParam('code');
        fetch(Vue.config.apiUrl + 'wechat/oauth/login?code=' + code, {
            method :'get',
            headers: {
                'Content-Type' : 'application/x-www-form-urlencoded',
            }
        })
        .then(respone => respone.json())
        .then(result => {
            if(result.status) {
                if(result.user.language == 'en') {
                    this.$i18n.set('en');
                    this.SetLanguage('en');
                }
                else {
                    this.$i18n.set('zh');
                    this.SetLanguage('zh');
                }
                
                this.$store.dispatch('Login', {user: result.user, token: result.token});
                Cookie.set('user-token', result.token, { expires: '1h' })
                Cookie.set('user', JSON.stringify(result.user), { expires: '1h' })
                
                this.onLoadCart();
                this.onLoadCollection();
            } else {
                this.$swal({
                    title             : '',
                    text              : '!',
                    type              : 'warning',
                    confirmButtonText : ''
                }).then(function() {
                    WeixinJSBridge.call('closeWindow');
                })
            }
        })
        .catch(err => {})

        fetch(Vue.config.apiUrl+ 'banner/all', {
            method :'get',
            headers: {
                'Content-Type' : 'application/x-www-form-urlencoded'
            }
        })
        .then(respone => respone.json())
        .then(result => this.InitBanner(result.data))
        .catch(err => console.log('Banner'))
        fetch(Vue.config.apiUrl+ 'article/all', {
            method :'get',
            headers: {
                'Content-Type' : 'application/x-www-form-urlencoded'
            }
        })
        .then(respone => respone.json())
        .then(result => {
            console.log('result', result, this.lang)
            this.InitArticle({articles:result.data})
        })
        .catch(err => console.log(''))

        fetch(Vue.config.apiUrl+ 'category/all', {
            method :'get',
            headers: {
                'Content-Type' : 'application/x-www-form-urlencoded'
            }
        })
        .then(respone => respone.json())
        .then(result => this.SetFilters({filters: result.data}))
        .catch(err => console.log(''))
    },
}



